/**
 * 获取天干索引
 * @param {String} str 
 * @returns 
 */
const getTianGanIndex=(str)=>{
  return TIANGAN.indexOf(str)+1;
}

/**
*
*甲，乙＝青龙
*丙，丁＝朱雀
*戊＝勾陈
*己＝腾蛇
*庚，辛＝白虎
*壬，癸＝玄武
 * @param {String} str 
 * @returns 
 */
export const getSixAnimal=(str)=>{
	let firstAnimal=''
	if(str==='甲'||str==='乙'){
		firstAnimal='青龙'
	}
	if(str==='丙'||str==='丁'){
		firstAnimal='朱雀'
	}
	if(str==='戊'){
		firstAnimal='勾陈'
	}
	if(str==='己'){
		firstAnimal='腾蛇'
	}
	if(str==='庚'||str==='辛'){
		firstAnimal='白虎'
	}
	if(str==='壬'||str==='癸'){
		firstAnimal='玄武'
	}
	let index=SIX_ANIMAL.indexOf(firstAnimal)
	let animalList=[]
	let count=0
	while(count<6){
		animalList.push(SIX_ANIMAL[index])
		index++;
		count++;
	}
  return animalList;
}



